export enum ContextMenusNames {
    CONTAINER_CONTEXT_MENU = 'CONTAINER_CONTEXT_MENU',
}

export type SchedulerStateType = {
    tableContentParams: TableContentParamsType

    beginDate: string
    activeChannelId: number

    viewTimeLineSize: number            // отображение временной линейки,
    viewDays: number
    viewDaysOfWeek: ViewDaysOfWeekType

    daysList: DaysListType         // Выводимые дни

    containersList: ContainerListType
    containersDragMode: boolean

    groupedContainersList: GroupedContainersListType

    viewSidePanel: boolean
    dragStartSidePanel: boolean
    draggedContainerId: string

    loadedContainersList: LoadedContainersType

    contextMenus: ContextMenusType

    selectedContainersList: SelectedContainersListType


}

// ==================================================================================

export type TableContentParamsType = {
    viewWidth: number
    contentWidth: number
    scrollLeft: number

    tableCellXpx: number            // Ширина по горизонтали
    tableCellYpx: number            // высота строки относительно ко времени
    // tableCellXcount: number         // Количество отображаемых дней в сетке
    // tableCellYcount: number         // количество строк
}

