export type ViewDaysOfWeekType = Array<string>

export type DaysListType = Array<string>

export type ContainerListType = {
    [id: string]: {
        // id: string
        logDate: string
        startTime: number               // время смещения от полуночи - sec
        chanelID: number                // канал идентификатора

        primaryText: string     // title
        secondaryText: string
        tagText: string
        objectType: string      // objectClass            // SER, EPS, PRO. RPO, RPT, EPR
        objectEpisodeInfo: string
        objectDuration: number  // duration                // sec - продолжительность
        color: string           // bgColor

        // intersectionIDs: Array<string>  // id соседей
    }
}

export type GroupedContainersListType = {
    [date: string]: DayGroupedContainersType,
}

export type DayGroupedContainersType = Array<GroupedContainersType>

export type GroupedContainersType = Array<string>


export type LoadedContainerType = {
    primaryText: string     // title
    secondaryText: string
    tagText: string
    objectType: string      // objectClass            // SER, EPS, PRO. RPO, RPT, EPR
    objectEpisodeInfo: string
    objectDuration: number  // duration                // sec - продолжительность
    color: string           // bgColor
}

export type LoadedContainersType = {
    [id: string]: LoadedContainerType,
}


export type ContextMenusType = {
    [key: string]: boolean
}


// export type SelectedContainersListType = Array<string>
export type SelectedContainersListType = {
    [id: string]: boolean
}


export type SelectWindowCoordsType = {
    x: number,
    y: number,
}


